@import url(https://fonts.googleapis.com/css2?family=Edu+QLD+Beginner:wght@400;600;700&display=swap);
.meter {
  height: 20px;
  position: relative;
  background: rgb(243, 239, 239);
  border-radius: 25px;
}

.meter>span {
  display: block;
  height: 100%;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: #aaa5ff;
  position: relative;
  overflow: hidden;
}

.skill {
  margin-top: 10px;
  line-height: 3.5vw;
}

.skill-bar {
  font-size: 28px;
  width: 40%;
}

.skills-container {
  margin: 0 auto;
  display: flex;
  padding: 0 10px;
  margin: 0px auto;
  margin-top: 2rem;
}

.skills-container>* {
  width: 40vw;
  flex: 1 1;
  margin-bottom: 30px;
}

.skills-image>img {
  margin-left: 80px;
  max-width: 90%;
  height: auto;
}

/* Media Query */

@media (max-width: 1456px) {
  .skills-bar {
    line-height: 4rem;
  }

  .skills-image {
    display: none;
    order: 2;
  }
}

@media (max-width: 768px) {
  .skills-container>* {
    width: 70vw;
  }

  .skills-container {
    flex-direction: column;
  }

  .skills-bar {
    line-height: 3rem;
    margin: 20px;
  }

  .skills-image {
    display: none;
    order: 2;
  }

  .skill>p {
    padding: 10px;
  }
}
.container {
    width: 250px;
    display: none;
    position: fixed;
    bottom: 0;
    left: 50%;
 transform: translate(-50%, -50%);
    justify-content: space-around;
    align-items: center;
    background: #1d1d1d;
    padding: 0 0.4em;
    color: #fff;
    border-radius: 20px;
    box-shadow: 0 0 10px #202020;
    z-index: 5;
}

.container::before{
    content:"";
    position: absolute;
    top: 5%;
    left: -2.5%;
    width: 105%;
    height: 112%;
    border-radius: 20px;
    background: linear-gradient(to right, #272727,#2a2a2a);
    z-index: -1;
    transform: background 4s;
}

.container:hover::before {
    background: linear-gradient(to right, #9d50bb,#6e48aa);
}

.box {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    cursor: pointer;
    transition: transform 0.4s;
}

.box:hover{
    transform: translateY(-10px);
    background: linear-gradient(to right,#9d50bb,#6e48aa);
}

i {
    cursor: pointer;
    transition: transform 0.4s;
}
.box:hover i {
    transform: scale(1.5);
}

@media screen and (max-width: 768px) {
    .container {
        display: flex;
    }
    
}
